import React, { useContext, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { contexto } from '../App';
import { Box, Grid, Container, Checkbox, FormControlLabel, TextField, Button } from '@mui/material';
import { bordesCeleste, estiloFondo, fondotransparent, titulo } from './color';

function Detail() {
  const { detalles, actualizarTarea, actualizarDescripcion } = useContext(contexto);
  const { id } = useParams();
  const tarea = detalles(id);
  const [editar, setEditar] = useState(true);
  const navegador = useNavigate();
  const fecha = tarea.fecha.toLocaleString();
  console.log(fecha);
  const redirect = () => {
    navegador(`/`);
  };

  const handleEdit = () =>
  {
    setEditar(!editar);
  }
  const handleChange = (event) =>
  {
    const descripcion = event.target.value;
    actualizarDescripcion(tarea.id, descripcion);
  }
  const handleCheck = (event) =>
  {
    const checked = event.target.checked;
    actualizarTarea(tarea.id, checked);
  }
  return (
    <Box sx={estiloFondo}>
      <Container sx={{display:"flex", justifyContent:"center", alignItems:"center", color:titulo}}>
          <Box sx={{...fondotransparent, display:"flex", borderRadius:"18px", height:"80vh", width:"50vh", border:`1px solid ${bordesCeleste}`}}>
            <Grid container rowSpacing={0}>
              <Grid item xs={12}>
                <Grid container marginTop={2} justifyContent="center">
                  <Grid item fontSize={{ xs: 32, md: 40 }}>
                    <div>{tarea.titulo}</div>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={12}>
                <Grid container marginTop={2} justifyContent="flex-end">
                  <Grid item marginRight="2%" fontSize={30}>
                  <FormControlLabel
                    control={<Checkbox size="large" checked={tarea.estado} onChange={handleCheck} sx={{color:titulo, '&.Mui-checked':{color:titulo}}} />}
                    label="Estado"
                    labelPlacement="start"
                  />
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={12}>
                <Grid container marginTop={2} justifyContent="flex-end">
                <TextField
                    fullWidth
                    sx={{
                      marginRight:"2%",
                      marginLeft:"2%",
                    }}
                    multiline
                    onChange={handleChange}
                    defaultValue={tarea.descripcionlarga}
                    InputProps={{
                      readOnly: editar,
                      style: { color: titulo },
                      disableUnderline: true,
                    }}
                    rows={10}
                    variant='standard'
                  />
                </Grid>
              </Grid>
              <Grid item xs={12}>
                <Grid container marginTop={2} justifyContent="space-between" sx={{marginLeft:"2%"}}>
                  <Grid item>
                    <FormControlLabel
                      control={<Checkbox checked={!editar} onChange={handleEdit} size='small' sx={{color:titulo, '&.Mui-checked':{color:titulo}}}/>}
                      label="editar"
                    >
                  </FormControlLabel>
                  </Grid>
                  <Grid item marginRight={4}>
                    <div style={{color:"rgba(16,110,168,0.8)"}}>Fecha de creacion: {fecha}</div>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={12}>
                <Grid container marginTop={2} justifyContent="center" sx={{marginLeft:"2%"}}>
                  <Grid item>
                    <Button variant='contained' onClick={redirect} color='info' ><div>Regresar al inicio</div></Button>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Box>
    </Container>
    </Box>
  );
}

export default Detail;
