import React, { useContext, useState } from 'react';
import { contexto } from '../App';
import { useNavigate } from "react-router-dom";
import { Grid, Button, Box, TextField, Container, Checkbox, FormControlLabel } from '@mui/material';
import {estiloFondo, cuadroCrear, bordesCeleste, textFieldOutlineEdit } from './color';

function Create() {
  const { tareas, actualizarContexto } = useContext(contexto);
  const [descripcion, setDescripcion] = useState('');
  const [titulo, setTitulo] = useState('');
  const [estado, setEstado] = useState(false);
  const navegador = useNavigate();
  const fecha = (new Date());
  const redirect = () => {
    navegador(`/`);
  };
  const handleSubmit = (e) => {
    const maxId = 1+(tareas.reduce((max, obj) => (obj.id > max ? obj.id : max), 0));
    e.preventDefault();
    const nuevaTarea = {
      id: maxId,
      titulo: titulo,
      descripcion: "",
      descripcionlarga: descripcion,
      estado: estado,
      fecha: fecha,
    };
    actualizarContexto(nuevaTarea);  
    setTitulo('');
    setDescripcion('');
    setEstado(false);
    redirect();
  };

  return (
    <Box sx={estiloFondo}>
      <Container sx={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
        <Box sx={{...cuadroCrear, border:`1px solid ${bordesCeleste}`}}>
          <form onSubmit={handleSubmit}>
            <Box sx={{ flexGrow: 1 }}>
              <Grid container spacing={2} justifyContent="center">
                <Grid item xs={12}>
                  <TextField
                    label="Titulo"
                    fullWidth
                    sx={textFieldOutlineEdit}
                    required
                    value={titulo}
                    onChange={(e) => setTitulo(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12} justifyContent="space-around" display="flex"> 
                  <FormControlLabel sx={{marginLeft:"10%",}} control={<Checkbox sx={{color:bordesCeleste}} checked={estado}   onChange={(event) => setEstado(event.target.checked)} />} label="Completado" />
                </Grid>
                <Grid item xs={12} sx={{ flexGrow: 1 }}>
                  <TextField
                    label="Descripcion larga"
                    fullWidth
                    required
                    sx={textFieldOutlineEdit}
                    multiline
                    value={descripcion}
                    rows={10}
                    onChange={(e) => setDescripcion(e.target.value)}
                  />
                </Grid>
              </Grid>
            </Box>
            <Box sx={{paddingTop:"10%"}}>
            <Grid item xs={12} sx={{ display: "flex", justifyContent: "center", justifyItems:"flex-start" }}>
                  <Button type="submit" variant="contained" color="primary">
                    Enviar
                  </Button>
                </Grid>
            </Box>
          </form>
        </Box>
      </Container>
    </Box>
  );
}

export default Create;
