import React, { useContext } from 'react';
import { contexto } from '../App';
import { useNavigate } from "react-router-dom";
import { Grid, List, ListItemText, Button, Box, Divider} from '@mui/material';
import {titulo, estiloFondo, fondotransparent, bordesCeleste} from './color';
function Home() {
  const { tareas } = useContext(contexto);
  const navegador = useNavigate();
  const redirect = (id, boolean) => {
    if(boolean)
    navegador(`/detail/${id}`);
    if(!boolean)
    navegador(`/create`);
  };

  return (
    <Box sx={{ ...estiloFondo, alignItems:"stretch"}}>
      <Grid
        container
        spacing={0}
        sx={{justifyContent:"center", alignItems:"center"}}
        direction="column"
      >
        <Box sx={{position:"absolute", top:"1%", WebkitBackgroundClip: 'text', backgroundImage: `linear-gradient(to bottom right, ${titulo}, #106BA3)`, color:"transparent"}}>
        <Grid item xs={12}><h1 style={{fontSize:"40px"}}>TAREAS</h1></Grid>
        </Box>
        <Box elevation={6} sx={{...fondotransparent, overflowY:"scroll", overflowX:"hidden", border:`1px solid ${bordesCeleste}`,width:"30%", height:"70%", borderRadius:"18px", display:"flex",justifyContent:"flex-start", alignItems:"flex-start", 
            '&::-webkit-scrollbar': {
              height:"20%",
              width: '5px',
            },
            '&::-webkit-scrollbar-thumb': {
              backgroundColor: bordesCeleste,
              marginLeft:"2px"
            },
            '&::-webkit-scrollbar-thumb:hover': {
              backgroundColor: bordesCeleste,
            },
            '&::-webkit-scrollbar-track': {
              backgroundColor: 'transparent',
            },}}>
        <Grid item xs={12} sm={12}>
          <List>
            {tareas.map((tarea, index) => (
              <Box sx={{ width:"99.8%", marginTop:"0px"}}>
              <Button onClick={()=> redirect(tarea.id, true)} TouchRippleProps={{ style: { color: 'black' } }} sx={{'&:hover': { backgroundColor: 'rgba()' },width:"100%",textAlign:"left"}}><ListItemText  sx={{color:titulo, '& .MuiListItemText-secondary': { color: "#106EA8" }}} key={tarea.id} primary={tarea.titulo} secondary={tarea.descripcion} /></Button>
              <Divider sx={{ backgroundColor: bordesCeleste, marginLeft:"1%"}}/>
              </Box>
            ))}
          </List>
        </Grid>
        </Box>
        <Box sx={{position:"absolute", bottom:"6%"}}>
        <Grid item xs>
          <Button variant='outlined' size='large' onClick={()=> redirect(null, false)} sx={{color:titulo, borderColor:titulo, '&:hover': { borderColor: '#0C507A', color:"#0C507A" }}}>Crear Tarea</Button>
        </Grid>
        </Box>
      </Grid>
      </Box>
  );
}

export default Home;
